.btn-primary {
  @include button($theme-color);
}

.btn-outline-primary {
  @include button-outline($theme-color);
}

.btn-secondary {
  @include button-variant(
    $body-color, gray('x-light'), darken(gray('x-light'), 6.5%), gray('light'), $border-color, none);
}

